/* MOBILE */

.navbar-mobile {
  display: block;
  z-index: 200;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5vw;
}

.navbar-mobile-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 2%;
}


/* MENU ICON */

.navbar-menu-icon {
  position: relative; 
  width: 4vw;
  height: 3vw;
  cursor: pointer;
  transition: transform 0.5s;
}

.navbar-menu-icon span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); 

  display: block;
  width: 100%;
  height: 0.4vw;
  transition: transform 0.5s;
}

.navbar-menu-icon span::before, .navbar-menu-icon span::after {
  content: '';
  position: absolute;
  top: 0;
  transform: translateY(-0.75vw);

  display: block;
  width: 100%;
  height: 100%;

  background-color: #eee;
  transition: transform 0.5s;
}

.navbar-menu-icon:hover span::before {
  background-color: #bbb;
}

.navbar-menu-icon:hover span::after {
  background-color: #bbb;
}

.navbar-menu-icon span::after {
  transform: translateY(0.75vw);
}

.navbar-menu-icon.is-opened span::before { 
  transform: translateY(0) rotate(45deg);
}

.navbar-menu-icon.is-opened span::after { 
  transform: translateY(0) rotate(-45deg);
}

.navbar-menu-icon.is-opened { 
  transform: rotate(90deg);
}




.navbar-logo-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.2vw 0;
}

.navbar-logo-img {
  z-index: 2;
  width: 3.5vw;
  border: solid 1px #fff;
  border-radius: 50%;
}

.navbar-logo-img-active {
  animation: rotation 3s infinite linear;
  -webkit-animation: rotation 3s infinite linear;
  animation-fill-mode: forwards;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.navbar-music-container {
  z-index: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  width: 25vw;
  background-color: rgba(5, 5, 5, 0.9);
  border-radius: 50vw;
  padding-left: 3.5vw;

  transform: translateX(-3.4vw);

  -webkit-animation: fadeInMusic 0.5s ease-out;
  -moz-animation: fadeInMusic 0.5s ease-out;
  -o-animation: fadeInMusic 0.5s ease-out;
  animation: fadeInMusic 0.5s ease-out;
}

@keyframes fadeInMusic {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

.audio-player {
  display: flex;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  padding-right: 0.5vw;
}

.track-info {
  width: 100%;
  position: relative;
  padding-left: 5%;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
}

.title {
  font-size: 1.5vw;
  margin-bottom: 2px;
  color: #ddd;
}

.artist {
  font-size: 1vw;
  color: #ddd;
}

.audio-controls {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin: 0;
}

.prev, 
.play, 
.pause,
.next {
  width: 3.5vw;
  height: 3.5vw;
  border-radius: 2vw;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 1.5vw;
  margin: 0;
  cursor: pointer;
  transition: background-color .3s ease;
  -webkit-transition: background-color .3s ease;
}

.prev:hover, 
.play:hover, 
.pause:hover,
.next:hover {
  background-color: #101010;
  transition: background-color .3s ease;
  -webkit-transition: background-color .3s ease;
}

.pause {
  background-image: url(data:image/svg+xml;utf8;base64,)
}

.prev {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

.play {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

.next {
  background-image: url(data:image/svg+xml;utf8;base64,)
}




/* SCREEN SIZE */

@media (max-width: 768px){

  .navbar-mobile {
    top: 0vw;
    width: 100%;
    height: 15vw;
  }

  .navbar-mobile-container {
    padding: 0 5%;
  }
  
  .navbar-logo-img {
    width: 10vw;
  }

  .navbar-mobile-logo-text {
    display: none;
  }

  .navbar-menu-icon {
    position: relative; 
    width: 10vw;
    height: 10vw;
    cursor: pointer;
    transition: transform 0.5s;
  }

  .navbar-menu-icon span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); 

    display: block;
    width: 100%;
    height: 0.8vw;
    transition: transform 0.5s;
  }

  .navbar-menu-icon span::before, .navbar-menu-icon span::after {
    content: '';
    position: absolute;
    top: 0;
    transform: translateY(-2vw);

    display: block;
    width: 100%;
    height: 100%;

    background-color: #eee;
    transition: transform 0.5s;
  }

  .navbar-menu-icon span::after {
    transform: translateY(2vw);
  }

  .navbar-menu-icon.is-opened span::before { 
    transform: translateY(0) rotate(45deg);
  }

  .navbar-menu-icon.is-opened span::after { 
    transform: translateY(0) rotate(-45deg);
  }

  .navbar-menu-icon.is-opened { 
    transform: rotate(90deg);
  }

  .navbar-menu-icon.is-opened{
    stroke-dashoffset: 0; 
  }

}